import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: [],
					payState: null,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: [],
					payState: 0,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 2,
					text: '待发货',
					loadingType: 'more',
					orderList: [],
					payState: 20,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 3,
					text: '待收货',
					loadingType: 'more',
					orderList: [],
					payState: 40,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 4,
					text: '待评价',
					loadingType: 'more',
					orderList: [],
					payState: 60,
					pageSize: 10,
					pageIndex: 1
				}
			],
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = options.state;
		this.loadData()
	},
	 
	methods: {
		navTo(url){
			uni.navigateTo({
				url
			})  
		},
		//获取订单列表
		async loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			if(navItem.loadingType == 'noMore'){
				return;
			}
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			
			let values = {needDetail: true,pageIndex: navItem.pageIndex,pageSize: navItem.pageSize};
			if(navItem.payState != null){
				values['payStatus'] = navItem.payState;
			}
			let result = await this.$request.ModelOrder.listOrder(values);
			let orderList = result.records;
			orderList.forEach(item=>{
				item = Object.assign(item, this.orderStateExp(item.payStatus));
				navItem.orderList.push(item);
			})
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			this.$set(navItem, 'loaded', true);
			
			//判断是否还有数据， 有改为 more， 没有改为noMore 
			if(orderList.length < navItem.pageSize){
				navItem.loadingType = 'noMore';
			}else{
				navItem.loadingType = 'more'; 
			}
			
			navItem.pageIndex = navItem.pageIndex + 1;
			this.navList[index] = navItem;
			console.log('this.navList[index]',this.navList[index]);
		}, 

		//swiper 切换
		changeTab(e){
			if(this.tabCurrentIndex == e.target.current){
				return;
			}
			this.tabCurrentIndex = e.target.current;
			this.loadData();
		},
		//顶部tab点击
		tabClick(index){
			if(this.tabCurrentIndex == index){
				return;
			}
			this.tabCurrentIndex = index;
			this.loadData();
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				let {stateTip, stateTipColor} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip, 
					stateTipColor
				})
				
				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val=>val.id === item.id);
				index !== -1 && list.splice(index, 1);
				
				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case -10:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
				case 0:
					stateTip = '待付款';break;
				case 20:
					stateTip = '待发货'; break;
				case 40:
					stateTip = '待收货'; break;
				case 60:
					stateTip = '已收货';break;
				case 80:
					stateTip = '待评价';break;
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
